.products {
  @media screen and (max-width: 767px) {
    padding-top: 121px;
    padding-bottom: 98px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-top: 122px;
    padding-bottom: 112px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 127px;
    padding-bottom: 119px;
  }
}

.section-subtitle {
  margin-bottom: 15px;

  @media screen and (max-width: 767px) {
    font-size: 18px;
    line-height: 1.167;
  }

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.154;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.133;
  }
}

.section-title {
  @media screen and (max-width: 767px) {
    margin-bottom: 144px;

    font-size: 34px;
    line-height: 1.147;
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 150px;

    font-size: 48px;
    line-height: 1.145;
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 259px;

    font-size: 58px;
    line-height: 1.32;
  }
}

.products__list {
  @media screen and (min-width: 768px) {
    display: flex;
    margin-right: -20px;
  }

  @media screen and (min-width: 1280px) {
    display: flex;
    margin-right: -24px;
  }
}

.products__item {
  text-align: center;
  border-radius: 24px;

  // после ширины экрана 320px контент не тянется

  @media screen and (max-width: 319px) {
    width: 100%;
  }

  @media screen and (min-width: 320px) {
    margin-left: auto;
    margin-right: auto;
    width: 280px;
  }

  // контент тянется до ширины экрана 480px

//   @media screen and (max-width: 439px) {
//       width: 100%;
//   }
//   @media screen and (min-width: 480px) {
//       margin-left: auto;
//       margin-right: auto;
//       width: 440px;
//   }

//   @media screen and (min-width: 768px) {
//     margin-right: 20px;
//     width: 220px;
//   }

//   @media screen and (min-width: 1280px) {
//     margin-right: 24px;
//     width: 335px;
//   }
}

@media screen and (max-width: 767px) {
  .products__item:nth-child(1) {
    margin-bottom: 130px;
    background-color: $bg-hero;
  }

  .products__item:nth-child(2) {
    background-color: $bg-section2;
    margin-bottom: 130px;
  }

  .products__item:last-child {
    background-color: $bg-section3;
  }
}

@media screen and (min-width: 768px) {
  .products__item:nth-child(1) {
    background-color: $bg-hero;
  }

  .products__item:nth-child(2) {
    background-color: $bg-section2;
  }

  .products__item:last-child {
    background-color: $bg-section3;
  }
}

.products__name {
  position: relative;

  font-family: $secondary-font;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $text-secondary;

  @media screen and (max-width: 767px) {
    padding-top: 161px;
    margin-bottom: 66px;

    font-size: 20px;
    line-height: 1.15;
  }

  @media screen and (min-width: 768px) {
    padding-top: 152px;
    margin-bottom: 66px;

    font-size: 20px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 163px;
    margin-bottom: 70px;

    font-size: 30px;
    line-height: 1.333;
  }
}

// img for mobile

@media screen and (max-width: 767px) {
  .products__name::before {
    content: '';
    position: absolute;
    width: 249px;
    height: 252px;
    top: -113px;
    left: calc((100% - 249px) / 2);

    background-size: 249px 252px;
  }

  .name--pink::before {
    background-image: url(../images/products/first-product-249.png);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/products/first-product-498.png);
    }

    @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
      background-image: url(../images/products/first-product-747.png);
    }
  }

  .name--coffee::before {
    background-image: url(../images/products/second-product-249.png);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/products/second-product-498.png);
    }

    @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
      background-image: url(../images/products/second-product-747.png);
    }
  }

  .name--chocolate::before {
    background-image: url(../images/products/third-product-249.png);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/products/third-product-498.png);
    }

    @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
      background-image: url(../images/products/third-product-747.png);
    }
  }

  .products__name::after {
    content: '';
    position: absolute;
    bottom: -36px;
    left: calc(100% / 2 - 13px);
    width: 26px;
    height: 6px;

    background-image: url(../images/icons/products-three-dots.svg);
  }
}

