    }

    @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
      background-image: url(../images/products/first-product-1005.png);
    }
  }

  .name--coffee::before {
    background-image: url(../images/products/second-product-335.png);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/products/second-product-670.png);
    }

    @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
      background-image: url(../images/products/second-product-1005.png);
    }
  }

  .name--chocolate::before {
    background-image: url(../images/products/third-product-335.png);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/products/third-product-670.png);
    }

    @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
      background-image: url(../images/products/third-product-1005.png);
    }
  }

  .products__name::after {
    content: '';
    position: absolute;
    bottom: -38px;
    left: calc(100% / 2 - 13px);
    width: 26px;
    height: 6px;

    background-image: url(../images/icons/products-three-dots.svg);
  }
}

.products__text {
  font-family: $primary-font;
  font-weight: 700;
  letter-spacing: 0.04em;
  text-align: center;

  color: $text-secondary;

  @media screen and (max-width: 767px) {
    padding-left: 40px;
    padding-right: 40px;
    width: 100%;
    min-height: 124px;

    font-size: 14px;
    line-height: 152%;
  }

  @media screen and (min-width: 768px) {
    padding-left: 11px;
    padding-right: 11px;
    max-height: 124px;

    font-size: 14px;
    line-height: 1.76;

    overflow: hidden;
  }

  @media screen and (min-width: 1280px) {
    padding-left: 44px;
    padding-right: 44px;
    height: 106px;

    font-size: 16px;
    line-height: 1.76;
  }
}

.products__btn {
  width: 40px;
  height: 40px;

  background-color: $text-secondary;
  border: none;
  border-radius: 50%;

  @media screen and (max-width: 767px) {
    margin-top: 30px;
    margin-bottom: 40px;
  }

  @media screen and (min-width: 768px) {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 62px;
    margin-bottom: 50px;
  }
}

.products__icon {
  fill: $primary-color;
}

